import axios from 'axios';

export const baseUrl = 'http://47.92.254.59:3000';

const axiosInstance = axios.create({
  baseURL: baseUrl,
  withCredentials: true
});

axiosInstance.interceptors.response.use(
  response => response.data
  ,
  error => {
    console.log(error, ' ');
  }
);

export {
  axiosInstance
};

